
import React, { useState, useEffect } from 'react'
//import './_styles4.css';
import ScrollPosition from './scroll-decorator';



const Hero= (props)=>{
 
    /*
    const {   goDark } = useObserveChanges();  
    const {  item } = props;
    */
    const [initialize, setInitialize] = useState(false);  
    const [position, setPosition] = useState(0);  
   
  
    useEffect(() => {      
      if(!initialize){
        setInitialize(true);
      }
    });
  
    let  opacity = 1-(position/500);
    const handleScroll = (e) => {
        if(e<950){
            setPosition(e);
        }
      }

    
    let heroTitle =  "Hacer lo que amas se convierte en pasion en vez de trabajo";
    heroTitle =  "Doing what you love becomes passion instead of work";
   
    let heroMP4 =  "https://videos.ctfassets.net/i5wc420v2vd1/3ST3WLEGpWUu2Ymg8WoEMs/06ee7becf93cce3083b115d2feb63a8b/Netflix-MainHero-V2.mp4" ;
    
    let isVideoHero = true;
    let imagePosition = position/1.5; 
    return (
    <div className={`heroHRM-row heroPortfolio `} style={{position: "relative", width: "100%", height: "100%", overflow: "hidden"}}>
        <ScrollPosition  scrollhandler={(e)=>handleScroll(e)}/>
        <div className="heroHRM ">
            <div className="heroHRM-motion">
                {isVideoHero?
                <div id="81323067"  style={{position: "relative", width: "100%", height: "100%", overflow: "hidden"}}>
                    <video  className={position?" player-view":""} style={{ transform: `matrix(1, 0, 0, 1, 0, ${0})`}}
                        autoPlay
                        loop
                        muted
                        playsInline
                        src={heroMP4}
                    />
                    <div className="player-timedtext" style={{display: "none", direction: "rtl"}}></div>
                </div>
                :null}
                
            </div>
            <div className="heroHRM-title textTransfrom"  style={{ opacity:opacity,  transform:`translate3d(0px, ${position*1.2}px, 0px) `}}>
                <div className="title-text">{`${heroTitle}`}</div>                                
            </div>
        </div>
        <div  className="gradient_2header btm_effect"></div>
    </div>
    )
}


export default Hero;