
import React, { Component } from 'react';
import './App.css';
import './stylesFonts.css';
import { HashRouter } from 'react-router-dom';
import Home from './component/init'

class App extends Component {
  constructor(props) {
    super(props);
    
    this.state = {
      route_list : [],
      setting:false,
      badUser:false
    };
  }


  render() {
    return (
      <div style={{ zIndex: 0, overflow: "hidden"}} >
          <HashRouter>
            <Home/>
          </HashRouter>
      </div>
    );
  }
}

export default App;
