

import React, { useEffect, useState } from 'react'
import { withRedux } from '../../store/redux'
import { useSelector, useDispatch } from 'react-redux'


import { withRouter} from 'react-router-dom';

// import * as _Route from 'react-router-dom';


import { SearchVideos, VideoFiles , getTitleDetailandSimilar, GetVideos2Init} from  '../../actions/videos'


import { OpenModal, OpenWatchDialog ,addVideo, getMoviesNtflx, getMoviesNtflxById, getMoviesHero} from '../../actions/common'



import * as _Util from '../../store/Util'

import '../_styles.css'
//import '../_styles5.css'
import RowSlideViewPort from '../rowSlideViewPort'

import DialogPlayer from '../operations/dialogPlayer'
import DialogVideoFile from '../operations/dialogVideoFile'

import HeroComponent from '../HeroCmp'

import CardH from '../CardsH'


const useObserveChanges = () => {
  const observeChanges =  useSelector(state => state.observeChanges);
  const forms =  useSelector(state => state.forms);
  const dispatch = useDispatch();  
  //let details  = _state['detailDepartmentByID'];

  let fileVideoSearch = useSelector(state => state.fileVideoSearch);

  let videoSearch = useSelector(state => state.videoSearch);

  const updForms= (form,field,v) => {
    let _forms = forms;
    if(!_forms[form]){
      _forms[form] = {};
    }
    _forms[form][field] = v;    
    dispatch({
      type: 'UPD_KEY_VALUE',
      kv:{key:'forms',value:_forms}
    })   
  }

  const getTitleInfo = (Id) => {
    getTitleDetailandSimilar(Id, dispatch);
  }


  const _getVideosSearch = (q,operation) => {
    SearchVideos(q, dispatch,  operation);
  }

  const _getVideoFiles = (q,operation) => {
    VideoFiles(q, dispatch,  operation);
  }

  const _getVideos2Init = () => {
    GetVideos2Init(dispatch,"getVideos2Init");
  }


  const _addVideo = (doc) => {
    addVideo(doc, dispatch,"getVideos2Init");
  }

  const updTitleView = (v,history) => {
    dispatch({
      type: 'UPD_KEY_VALUE',
      kv:{key:'videosTitleDetail',value:v}
    })    
  }



 const OpenOptions = (items, _ID) => {

  let data = {};
  data['zIndex']=150; 
  data['height']=items.metadata.height<480?(items.metadata.height)*1+80:480;
  data['width']=(items.metadata.width)*1+80; 
  data['props']={title:"Options", id: _ID, item:items};
  data['content'] = DialogVideoFile; 
  OpenModal(dispatch,data);
}



  const OpenPlayer = (items, _ID) => {

    let data = {};
    data['zIndex']=150;
    data['observeResize']=true;   
    data['height']=items.metadata.height<480?(items.metadata.height)*1+80:480;
    data['width']=(items.metadata.width)*1+80; 
    data['props']={title:"", id: _ID, items:items};
    data['content'] = DialogPlayer; 
    OpenModal(dispatch,data);
  }






  const _addVideo22 = (items, _ID) => {
    let data = {};
    data['zIndex']=150;
    data['observeResize']=true;
    data['props']={title:"", id: _ID, items:items};
    //data['content'] = AddVideo; 
    OpenModal(dispatch,data);
  }





  const _getVideosNtflxById= (q,operation) => {
    getMoviesNtflxById(q, dispatch, operation);
  }

  
  const _getMoviesHero= (q,operation) => {
    getMoviesHero(q, dispatch, operation);
  }



  const _getVideosNtflx = (q,operation) => {
    q["q"] = operation;
    getMoviesNtflx(q, dispatch, operation);
  }




  const _openMd = (_id, item) => {
    let data = {};
    data['zIndex']=450;
    data['Id']=_id;
    data['observeResize']=true;    
    data['props']={item:item}; 

    OpenWatchDialog(dispatch,data);
  }
  


  return { 
    observeChanges, 
    updTitleView,
    forms, 
    updForms, 
    dispatch,
    OpenPlayer,
    OpenOptions,
    _addVideo,
    fileVideoSearch,
    _getVideosNtflxById,
    _getMoviesHero,
    _getVideosNtflx,
    videoSearch,
    getTitleInfo,
    _openMd,
    _getVideoFiles,
    _getVideosSearch,
    _getVideos2Init
  }
}




  let lastSearch = 0;

  const handleSearch= (e,operationType,_searchProducts) => {
  let _now = (new Date()).getTime();
      lastSearch = _now;
      let _q_ = e;
      setTimeout(()=>{
        if(_now-lastSearch>=0){         
          _searchProducts(_q_, operationType);
        }
      },500)
     return true;
  }
  
  














const BrowseComponent = (props) => {
  const { 
    _getVideos2Init,
    _getMoviesHero,
    _addVideo,
    _getVideosNtflx,
    _getVideosNtflxById,
    _openMd
  } = useObserveChanges();

  let _state = _Util.getStore();
  let keys = _Util.getGlobalsKeys()
  _state["keys"] = keys;
 

  const [initialize, setInitialize] = useState(false);

  const [widthScreen, setWidthScreen] = useState(null);
   
  
  const searchHash = window.location.search.split('?')[1]?window.location.search.split('?')[1]:null;
  const typeBrowse = window.location.hash.split('/')[1];


  const router = _Util.parseQuery(searchHash);
  
  let outerWidth = _state["outerWidth"];

  let tabs = _Util.getTabs(outerWidth);
  
  
  let _heroMovies =  {};



  useEffect(() => {  
    if(widthScreen!==outerWidth){
      setWidthScreen(outerWidth);
      
    } 
    if(!initialize){
      setInitialize(true);
      //addingSeason()
    }
  });
  


  let _movieList = {};
 

 

  let  _videosByGenreTabs = _Util.groupByTabs(_movieList,36)

  const OpenExamples = (g) => {
    _openMd()
  }
  


    return (
      <>
      <div className={`main2View`} >
        {_heroMovies ? <HeroComponent  item={_heroMovies}  />:null}
        <div className={`lolomo`}>  

        {false && _videosByGenreTabs &&   _Util.ObjectKeys(_videosByGenreTabs).map((_grTGid,_grTGid_ind)=>{
          //let _gid = `${keys[19]}_${_grTGid}_grpoup_lowRow`; 
          //let isVgrp = _grTGid_ind>=start-4 && _grTGid_ind<=limit+2;
          if(_grTGid_ind<=20){
            return(
              <RowSlideViewPort  
                data={_videosByGenreTabs[_grTGid]} 
                keyCode={_state.keys[64]}  
                typeBrowse={typeBrowse} 
                _key_={_grTGid_ind} 
                _ID={_grTGid} 
                //updLastIndex={(e)=>updLastIndex(e)}
              />
            )
          }else{
            return null
          }
          
        })}
         

         <CardH 
            title={`About my Self `}
            comment={[
              `My name Is Hector Ricardo. I'm from Cuba and I have a beutiful family.`,
              `I love spending time with my family and I thank God for having them by my side`,
              `One of my big dream is become a great web developer`,
            ]}
            src={`https://firebasestorage.googleapis.com/v0/b/hrm-1128.appspot.com/o/images%2Ffamily2_1200x800.jpg?alt=media&token=6420ac25-d1de-418e-b58b-559b513b43a3`}
            btnText={"See more "}
            color={"blue"}
         />

          <CardH 
            title={`Dedication, Responsibility and Commitment`}
            comment={[`I like to commit to what I do, always alert to the experience of others and learn something new every day.`]}
            sr2={`https://images.ctfassets.net/i5wc420v2vd1/6482BfPUYgw9GqcnxeKXUr/b7bd0b29262020a078b615d7af8eb328/Web-7187.jpg?w=600`}
            src={`https://firebasestorage.googleapis.com/v0/b/hrm-1128.appspot.com/o/images%2Fdedication.jpg?alt=media&token=fce68543-a1e5-4be0-93c7-ad65469ec212`}
            
            color={"blue"}
            blackTheme={true}
            btnText={"Contact Me "}
          />

          <CardH 
            title={`Experience `}
            comment={[
              `I have about 4 years experience on my Country .`,
              `I am one of those who thinks that actions in this case "Code" say more than words here you can see a couple of examples developed with React-Redux`
             
            ]}
            src={`https://firebasestorage.googleapis.com/v0/b/hrm-1128.appspot.com/o/images%2F2hrmfinance.png?alt=media&token=07d92793-d167-4fea-9ca2-74b72f96f5eb`}
            btnText={"See example "}
            color={"blue"}
            clickEvent={()=>OpenExamples(_Util.gen12CodeId())}
         />



          </div>
        </div>       
      </>
    );
  
}  












export default withRouter(withRedux(BrowseComponent))










