const _styleInit = (idWatch) => {  
  return `
  
  ._${idWatch}{
    --heliumPlayer__color_fire :#e50914;
    --heliumPlayer__color_fire_2 :#bf1315;
    --heliumPlayer__color_shadow: #454545;
    --heliumPlayer__color_dark_2: #2e2e2e; 
    --heliumPlayer__color_dark_9_7: #979797; 
    --heliumPlayer__color_dark_14: #141414;
    --heliumPlayer__color_light_9: #d9d9d9;
    --heliumPlayer__color_light_2: #a2a2a2;
    --heliumPlayer__color_light_4: #b4b4b4;
    --heliumPlayer__color_light_5: #d5d5d5;
    --heliumPlayer__color_light_6: #f6f6f6;
    --heliumPlayer__color_white_: #ffffff;
    --heliumPlayer__color_blck_: #000000;
    --heliumPlayer__bckcolor_blck_opacity_:rgba(10,10,10,.695);
  }

  .control-container_${idWatch}.active{
    transform: scale(1);
    opacity: 1;
    z-index: 5;
    /*visibility: visible;*/
  }

  .control-container_${idWatch}{   
    opacity: 0;
    position: absolute;
    -webkit-transform: scale(1) translateZ(0);
    -moz-transform: scale(1) translateZ(0);
    transform: scale(1) translateZ(0);
    -webkit-transition: opacity 160ms ease,visibility 0s linear 160ms,-webkit-transform .2s ease;
    transition: opacity 160ms ease,visibility 0s linear 160ms,-webkit-transform .2s ease;
    -o-transition: opacity 160ms ease,visibility 0s linear 160ms,-o-transform .2s ease;
    -moz-transition: opacity 160ms ease,transform .2s ease,visibility 0s linear 160ms,-moz-transform .2s ease;
    transition: opacity 160ms ease,transform .2s ease,visibility 0s linear 160ms;
    transition: opacity 160ms ease,transform .2s ease,visibility 0s linear 160ms,-webkit-transform .2s ease,-moz-transform .2s ease,-o-transform .2s ease;
    /*visibility: hidden;*/
    will-change: transform,opacity;
    height: 100%;
    width: 100%; 
    z-index: 5;  
  }

  .video-container_${idWatch}{
    position: absolute;
    z-index: 2;  
    height: 100%;
    width: 100%; 
  }

  .control-bottom-box_${idWatch}{
    display : block;     
    bottom : 3vh; 
    position: absolute; 
    left: 12px;
    right:12px; 
  }

  .control-left-box_${idWatch}{
    display : block;
    position: absolute; 
    left: 12px;
    width: 3em; 
    height: 100vh;   
  }

  .go--Back_${idWatch}{
    position: relative;
    top: 1em;
  }

  `
}





const useObserveChanges = () => {
  const observeChanges =  useSelector(state => state.observeChanges);
  const _state =  {};  
  const rdxOK = useSelector(state => state.rdxOK);
  const goDark = useSelector(state => state.goDark);
  
  let _appLoaded =  useSelector(state => state.appLoaded);
  const dispatch = useDispatch();
  const updKV= (k,v) => {
    dispatch({
      type: 'UPD_KEY_VALUE',
      kv:{key:k,value:v}
    })  
    dispatch({
      type: 'UPD_KEY_VALUE',
      kv:{key:observeChanges,value:_Util.gen12CodeId()}
    })  
  }

  const getState= () => {
    return _state;
  }



  if(!rdxOK){
    UpdateRdx(dispatch, getState )
  }
   
  
  return {  updKV,  _appLoaded, goDark }
}





const Browser= (props)=>{
 
  const {  updKV, goDark } = useObserveChanges();


  let _state = _Util.getStore();
  let keys = _Util.getGlobalsKeys()  
  let idWatch = "";
  let classN = ""
 
  const _goDark = goDark?1:0; 
 const [initialize, setInitialize] = useState(false);  
 const [isready, setIsready] = useState(false);  


  useEffect(() => {
    let _store = _Util.getStore();    
    if(props.history && !_store["route_history"]){
      _Util.updStore('route_history',props.history);
      updKV("route_history",props.history);
    }
    if(!initialize){
      window.scrollTo(0,0)
      setInitialize(true);      
      _Util.initConfig().then(d=>{
        setTimeout(()=>{
          _Util.getBrowser();         
          idWatch = keys[99];
          classN = _styleInit(idWatch)
          setIsready(true)
          if(props.location && props.location.pathname ===  "/"){
            props.history.push({pathname:"/browse"})
          }
          _Util.updStore('imageBlob',false);          
          window.scrollTo(0,0)
        },250)
      })
    }
  });

let userA = _Util.getBrowser();
  
let isMobile = userA.os === "Android" || userA.os === "iPhone" || _state["outerWidth"]<550 ;


let appLoaded = true || _state["appLoaded"];
    return (
      <>
      {}       
      <Header />
      <div  id={keys[90]} is-mobile={`${isMobile}`} >
      {isready && appLoaded && 
        <div className={!isMobile?'_app_body_content_':""}> 
          <>    
          <Switch>
            <div id={`data_ui_${keys[93]}`} style={{position:"static"}}>              
              <Route path="/browse" component={Browse} /> 
            </div>
          </Switch>
          {isready?
          <Footer />
          :null}
          </>
        </div>
        }        
        <style>{classN}</style>        
        </div>
        <div className="backDrop"  tabIndex="-1" style={{opacity: _goDark, zIndex:_goDark?5000:-1}}>
          <div className="previewModal--backDrop" tabindex="-1" data-uia="previewModal--backDrop" style={{display: _goDark?"block":"none"}}></div>
        </div>
        <DialogModal/>
       
      </>
    )
  
} 


export default  withRouter(withRedux(Browser))





