import React from 'react'


const CardS = (props) => {

  const {title, comment, src, color, blackTheme, btnText} = props;
  let colorClass = color || "dark";

  const clickEvent = () => {
    if(typeof props.clickEvent === "function"){
      props.clickEvent();
    }
  }

  return( 
    <div className=" _dsplFlx2 spaceAround wrappCard">
      <div className={`_dsplFlx2 boxCardFlex ${blackTheme?"isBlack":"light"}`}>
          {blackTheme?
          <div className="rightSide">
            <div className={`gradient ${colorClass}`}/>
            <img src={src} alt={title} fill="false"/>
          </div>:
          <div className="leftSide">
            <h2 color="Light" class="title_lft">{title}</h2>
              {comment && comment.map(cmt=>{
                return <div className="text_lft">{cmt} </div>
              })}
              <div className="action">
                <div  className={`_btn ${colorClass}`} onClick={()=>clickEvent()}>
                  <span>
                    {btnText}
                  </span>
                </div>
              </div>
          </div>
          }
          <div className={`centerSide ${colorClass}`}/>          
          {!blackTheme?
          <div className="rightSide">
            <div className={`gradient ${colorClass}`}/>
            <img src={src} alt={title} fill="false"/>
          </div>:
          <div className="leftSide">
              <h2 color="Light" class="title_lft">{title}</h2>
              {comment && comment.map(cmt=>{
                return <div className="text_lft">{cmt} </div>
              })}
              <div className="action">
                <div  className={`_btn ${colorClass}`}  onClick={()=>clickEvent()}>
                  <span>
                    {btnText}
                  </span>
                </div>
              </div>
          </div>
          }
      </div>
    </div>
  )

}

export default CardS