const initialState = {
  lastUpdate: 0,
  light: false,
  count: 0,
  listBezel: {},  
  operations:null,
  data: {},  
  forms: {},
  observeChanges:'Init01',
  observeImage:0
}






const reducer = (state = initialState, action) => {
  switch (action.type) {
    case 'TICK':
      return {
        ...state,
        lastUpdate: action.lastUpdate,
        light: !!action.light,
      }
    case 'INCREMENT':
      return {
        ...state,
        count: state.count + 1,
      }
    case 'DECREMENT':
      return {
        ...state,
        count: state.count - 1,
      }
    case 'RESET':
      return {
        ...state,
        count: initialState.count,
      }
    case "UPD_KEY_VALUE":
      return {
          ...state,
          [action.kv.key]: action.kv.value
      };
    default:
      return state
  }
}

export const initializeStore = (preloadedState = initialState) => {
  return createStore(
    reducer,
    preloadedState,
    composeWithDevTools(applyMiddleware())
  )
}

